@mixin reset-img {
  display: block;
  width: 100%;
  height: auto;
}

@mixin reset-link {
  // font: inherit;
  color: inherit;
  text-decoration: none;

}

@mixin reset-btn-push {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border: none;
  font: inherit;
  // color: inherit;
  background-color: transparent;
  cursor: pointer;
}

@mixin reset-btn-link {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: inherit;
}

@mixin reset-ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin reset-p {
  margin: 0;
}


@mixin reset-select {
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  cursor: pointer;
}
